<template>
  <div class="home">
    <header class="header">
      <img src="../../assets/home.jpg" alt="">
    </header>
    <!-- 内容部分 -->
    <div>
      <!-- 栏目6个 -->
      <van-grid :column-num="3">
        <van-grid-item
        v-for="value in categories"
        :key="value.id"
        :icon="value.icon"
        :text="value.name"
        />
      </van-grid>
      <!-- 产品4个 -->
      <van-grid :column-num="2" icon-size="350px">
        <van-grid-item
        v-for="value in products"
        :key="value.id"
        :icon="value.photo"
        :text="value.name"
        />
      </van-grid>
    </div>
  </div>
</template>
<script>
import {get,post} from '../../http/axios';
export default {
  methods:{
    //加载栏目信息
    localCategories(){
      let url = "/category/findAll";
      get(url).then((response)=>{
        //将查询结果，数组中的前6个
        this.categories=response.data.slice(0,6);
      })
    },
    //加载产品信息
    localProducts(){
      let url = "/product/query"
      let params = {
        page:0,
        pageSize:10
      }
      post(url,params).then((response)=>{
        this.products = response.data.list;
      })
    }
  },
  data(){
    return{
      categories:[],
      products:[]
    }
  },
  created(){
    //查询栏目
    this.localCategories();
    //查询产品
    this.localProducts();
  }
}
</script>
<style scoped>
.home {
  padding-bottom: 50px;
}
.header {
  height: 300px;
  overflow: hidden;
}
.header img {
  width: 100%;
}
</style>
